import React, { useState } from 'react';
import {
  Box,
  Paper,
  Typography,
  Tabs,
  Tab,
  Grid,
  Card,
  CardContent,
  Chip,
  List,
  ListItem,
  ListItemText,
  Divider,
  Button,
  Alert,
} from '@mui/material';
import {
  LocalHospital,
  Science,
  Assignment,
  Image,
  Timer,
} from '@mui/icons-material';
import { Patient } from '../../types/doctor';

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`patient-record-tabpanel-${index}`}
      aria-labelledby={`patient-record-tab-${index}`}
      {...other}
    >
      {value === index && <Box sx={{ py: 3 }}>{children}</Box>}
    </div>
  );
}

interface PatientRecordViewProps {
  patient: Patient;
  expiryTime: string;
  onSessionEnd: () => void;
}

const PatientRecordView: React.FC<PatientRecordViewProps> = ({
  patient,
  expiryTime,
  onSessionEnd,
}) => {
  const [tabValue, setTabValue] = useState(0);

  const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
    setTabValue(newValue);
  };

  // Mock data
  const mockRecords = {
    medications: [
      { name: 'Lisinopril', dosage: '10mg', frequency: 'Daily', status: 'active' },
      { name: 'Metformin', dosage: '500mg', frequency: 'Twice daily', status: 'active' },
    ],
    labResults: [
      { test: 'Complete Blood Count', date: '2024-02-15', status: 'final' },
      { test: 'Lipid Panel', date: '2024-02-15', status: 'final' },
    ],
    imaging: [
      { type: 'Chest X-Ray', date: '2024-02-10', status: 'final' },
      { type: 'MRI Brain', date: '2024-01-20', status: 'final' },
    ],
    notes: [
      { title: 'Annual Physical', date: '2024-02-15', provider: 'Dr. Smith' },
      { title: 'Follow-up Visit', date: '2024-01-20', provider: 'Dr. Johnson' },
    ],
  };

  return (
    <Box>
      <Alert severity="warning" sx={{ mb: 3 }}>
        <Typography variant="body1">
          Session expires at: {new Date(expiryTime).toLocaleTimeString()}
        </Typography>
      </Alert>

      <Paper sx={{ mb: 3, p: 3 }}>
        <Grid container spacing={2} alignItems="center">
          <Grid item xs={12} md={6}>
            <Typography variant="h5" gutterBottom>
              {patient.name}
            </Typography>
            <Typography variant="body1" color="text.secondary">
              Health Card: {patient.healthCardNumber}
              <br />
              DOB: {new Date(patient.dateOfBirth).toLocaleDateString()}
              <br />
              Province: {patient.homeProvince}
            </Typography>
          </Grid>
          <Grid item xs={12} md={6} sx={{ textAlign: 'right' }}>
            <Button
              variant="outlined"
              color="error"
              startIcon={<Timer />}
              onClick={onSessionEnd}
            >
              End Session
            </Button>
          </Grid>
        </Grid>
      </Paper>

      <Paper sx={{ width: '100%' }}>
        <Tabs
          value={tabValue}
          onChange={handleTabChange}
          aria-label="patient records tabs"
          sx={{ borderBottom: 1, borderColor: 'divider' }}
        >
          <Tab icon={<LocalHospital />} label="Medications" />
          <Tab icon={<Science />} label="Lab Results" />
          <Tab icon={<Image />} label="Imaging" />
          <Tab icon={<Assignment />} label="Clinical Notes" />
        </Tabs>

        <TabPanel value={tabValue} index={0}>
          <List>
            {mockRecords.medications.map((med, index) => (
              <React.Fragment key={index}>
                <ListItem>
                  <ListItemText
                    primary={
                      <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                        {med.name}
                        <Chip
                          label={med.status}
                          color={med.status === 'active' ? 'success' : 'default'}
                          size="small"
                        />
                      </Box>
                    }
                    secondary={`${med.dosage} - ${med.frequency}`}
                  />
                </ListItem>
                {index < mockRecords.medications.length - 1 && <Divider />}
              </React.Fragment>
            ))}
          </List>
        </TabPanel>

        <TabPanel value={tabValue} index={1}>
          <Grid container spacing={2}>
            {mockRecords.labResults.map((result, index) => (
              <Grid item xs={12} md={6} key={index}>
                <Card>
                  <CardContent>
                    <Typography variant="h6" gutterBottom>
                      {result.test}
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                      Date: {new Date(result.date).toLocaleDateString()}
                    </Typography>
                    <Chip
                      label={result.status}
                      color="success"
                      size="small"
                      sx={{ mt: 1 }}
                    />
                  </CardContent>
                </Card>
              </Grid>
            ))}
          </Grid>
        </TabPanel>

        <TabPanel value={tabValue} index={2}>
          <Grid container spacing={2}>
            {mockRecords.imaging.map((image, index) => (
              <Grid item xs={12} md={6} key={index}>
                <Card>
                  <CardContent>
                    <Typography variant="h6" gutterBottom>
                      {image.type}
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                      Date: {new Date(image.date).toLocaleDateString()}
                    </Typography>
                    <Chip
                      label={image.status}
                      color="success"
                      size="small"
                      sx={{ mt: 1 }}
                    />
                  </CardContent>
                </Card>
              </Grid>
            ))}
          </Grid>
        </TabPanel>

        <TabPanel value={tabValue} index={3}>
          <List>
            {mockRecords.notes.map((note, index) => (
              <React.Fragment key={index}>
                <ListItem>
                  <ListItemText
                    primary={note.title}
                    secondary={
                      <>
                        {new Date(note.date).toLocaleDateString()} - {note.provider}
                      </>
                    }
                  />
                </ListItem>
                {index < mockRecords.notes.length - 1 && <Divider />}
              </React.Fragment>
            ))}
          </List>
        </TabPanel>
      </Paper>
    </Box>
  );
};

export default PatientRecordView; 